
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
components: {
XHeader
},
mounted() {
if (this.$route.params.CBM) {
this.getDetail();
} else {
this.getDetailWithType();
}
},
data() {
return {
info: {},
CXYNR: ""
};
},
methods: {

getDetailWithType() {
this.$vux.loading.show({
text: "加载中..."
});
this.$ajax
.post(this.$root.host, {
"MSH.1": "server",
"MSH.2": "getByType",
type: "1",
})
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (typeof res != "undefined") {
this.info = res;
this.CXYNR = res.CXYNR;
}
})
.catch(error => {
console.log(error);
this.$vux.loading.hide();
});
},

getDetail() {
this.$vux.loading.show({
text: "加载中..."
});
this.$ajax
.post(this.$root.host, {
"MSH.1": "server",
"MSH.2": "agreementServiceDetail",
CBM: this.$route.params.CBM
})
.then(res => {
console.log(res);
this.$vux.loading.hide();
if (typeof res != "undefined") {
this.info = res;
this.CXYNR = res.CXYNR;
}
})
.catch(error => {
console.log(error);
this.$vux.loading.hide();
});
}
}
};



